.Reasons{
    padding: 0 2rem;
    display: flex;
    gap: 2rem;
}

.left-r{
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
}
.left-r>img{
    object-fit: cover;
}
 
.left-r>:nth-child(1)
{
    width: 12rem;
    grid-row: 1/3;
    height: 28rem;
}
.left-r>:nth-child(2)
{
    width: auto;
    height: 16rem;
    grid-column: 2/4;
}
.left-r>:nth-child(3)
{
    width: 14rem;
    grid-row: 2;
    grid-column: 2/3;
}
.left-r>:nth-child(4)
{
    width: 10rem;
    grid-row: 2;
    grid-column: 3/4;
    height: 11.5rem;
}
.right-r{
    flex: 1 1;
    gap: 1rem;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
}
.right-r>span{
    font-weight: bold;
    color:var(--orange);

}
.right-r>div{
    color: white;
    font-size: 3rem;
    font-weight: bold;
}

.detailes-r{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.detailes-r>div{
    display: flex;
    font-size: 1rem;
    gap: 1rem;
}
.detailes-r>div>img{
    width: 2rem;
    height: 2rem;
}

.partners{
    display: flex;
    gap:1rem;
}
.partners>img{
    width: 2.5rem;
}